export default {
	data() {
		return {
			imgList:[],
			content:''

		}
	},
	methods: {
		selectImg(){
			let count  = 9 - this.imgList.length
			uni.chooseImage({
				count,
				success:(e)=>{
					let tempFilePaths=e.tempFilePaths
					tempFilePaths.forEach((item,index)=>{
						if(count>index){
							this.imgList.push({
								url:item
							})
						}
					})
				}
			})
		},
		cancelSlect(index){
			this.imgList.splice(index,1)
		},
		async submit(){
			let imgPaths=[]
			uni.showLoading({
				title:"上传中..."
			})
			if(!this.content){
				uni.hideLoading()
				uni.showToast({
					title:"请输入反馈意见",
					icon:"none"
				})
				return
			}
			
			// 遍历imgList,准备上传文件
			for(let i=0;i<this.imgList.length;i++){
				let filePath=this.imgList[i].url
				filePath=await this.uploadImg(filePath)
				imgPaths.push(filePath)
			}
			this.postFeedback(this.content,imgPaths)
		},
		// 上传图片获取图片在云端的路径
		async uploadImg(filePath){
			const result=await uniCloud.uploadFile({
				filePath,
			})
			return result.fileID
		},
		postFeedback(content,imgPaths){
			this.$api.postFeedbackC({content,imgPaths}).then(res=>{
				uni.hideLoading()
				uni.showToast({
					title:"上传成功,2s后将返回个人中心",
					icon:"none"
				})
				setTimeout(()=>{
					uni.switchTab({
						url:'../tabbar/my/my'
					})
				},2000)
			}).catch((err)=>{
				console.log(err)
				uni.hideLoading()
				uni.showToast({
					title:"上传失败",
					icon:"none"
				})
			})
		}

	}
}
